<script lang="ts">
  import { afterUpdate, onMount, setContext } from "svelte";
  import { Streamlit } from "./streamlit";
  import { callMSGraph } from "./azure/graph";

  import { LabelMap, defaultLabels } from "./localization";
  import { PublicClientApplication, LogLevel } from "@azure/msal-browser";

  // variaveis para input
  export let inputClientId: string;
  export let inputAuthority: string;
  export let inputredirectUri: string;
  export let inputCustomLabels: LabelMap = {};

  // variaveis para retorno
  let authenticated: boolean = false;
  let accessToken: any;
  let expireDateToken: any;
  let user: any = {
    name: String,
    username: String,
    groups: String,
  };

  // Set up the response to Streamlit
  const setComponentValue = async (): Promise<void> => {
    let value;
    if (authenticated) {
      value = {
        authenticated: true,
        token: accessToken,
        _token_expire_date: expireDateToken,
        user: user,
      };
    } else {
      value = { authenticated: false };
    }
    console.log(value);
    Streamlit.setComponentValue(value);
  };

  const msalConfig = {
    auth: {
      clientId: inputClientId,
      authority: inputAuthority,
      redirectUri: inputredirectUri,
    },
    cache: {
      cacheLocation: "sessionStorage", // This configures where your cache will be stored
      storeAuthStateInCookie: false, // Set this to "true" if you are having issues on IE11 or Edge
    },
    system: {
      loggerOptions: {
        loggerCallback: (level: any, message: any, containsPii: any) => {
          if (containsPii) {
            return;
          }
          switch (level) {
            case LogLevel.Error:
              console.error(message);
              return;
            case LogLevel.Info:
              console.info(message);
              return;
            case LogLevel.Verbose:
              console.debug(message);
              return;
            case LogLevel.Warning:
              console.warn(message);
              return;
          }
        },
      },
    },
  };

  const graphConfig = {
    graphGroupsEndpoint:
      "https://graph.microsoft.com/v1.0/me/transitiveMemberOf",
  };

  const loginRequest = {
    scopes: ["openid", "profile", "User.Read"],
  };

  const MSALObj = new PublicClientApplication(msalConfig);

  const getUserGroups = (token: string) => {
    return new Promise((resolve, reject) => {
      callMSGraph(graphConfig.graphGroupsEndpoint, token, (data: any) => {
        let groups_list: any = [];
        data.value.forEach((group: any) =>
          groups_list.push(group["displayName"])
        );
        resolve(groups_list);
      });
    });
  };

  const getAccessToken = async () => {
    const accounts = MSALObj.getAllAccounts();
    if (accounts.length == 1) {
      const tokenResponse = await MSALObj.acquireTokenSilent({
        scopes: loginRequest.scopes,
        account: accounts[0],
      });

      const groups = await getUserGroups(tokenResponse.accessToken);

      accessToken = tokenResponse.accessToken;
      expireDateToken = tokenResponse.expiresOn;
      authenticated = true;

      user.name = accounts[0].name;
      user.username = accounts[0].username;
      user.groups = groups;

      console.log(user);
    }
    setComponentValue();
  };

  const login = () => {
    MSALObj.loginPopup(loginRequest)
      .then((response) => {
        const account = response.account;
        getAccessToken();
      })
      .catch((error) => {
        console.error(error);
      });
  };

  const logout = () => {
    // Realize o logout aqui
    // Por exemplo, você pode chamar uma função de logout da biblioteca MSAL
    const logoutRequest = {
      account: MSALObj.getAccountByUsername(user.username),
      postLogoutRedirectUri: msalConfig.auth.redirectUri,
    };
    MSALObj.logoutPopup(logoutRequest);

    accessToken = null;
    authenticated = false;

    user.name = null;
    user.username = null;
    user.groups = null;

    setComponentValue();
  };

  onMount(() => {
    Streamlit.setFrameHeight();
    // getAccessToken();
  });

  afterUpdate(() => {
    Streamlit.setFrameHeight(clientHeight);
  });

  let clientHeight: number;

  const labels = {
    ...defaultLabels,
    ...inputCustomLabels,
  };

  setContext("localization", labels);
</script>

<div bind:clientHeight>
  {#if authenticated}
    <div class="alert alert-success">
      <div>
        <button
          type="button"
          class="btn btn-sm btn-secondary d-block"
          on:click={logout}
        >
          <span>{labels.labelButtonOut}</span>
        </button>
        <p class="mt-2 d-block">{labels.labelLogout + user.username}</p>
      </div>
    </div>
  {:else}
    <div class="alert alert-info">
      <button
        type="button"
        class="btn btn-sm btn-secondary d-block"
        on:click={login}
      >
        <span>{labels.labelButtonIn}</span>
      </button>
      <p class="mt-2 d-block">{labels.labelLogin}</p>
    </div>
  {/if}

  {#await getAccessToken()}
    <div class="alert alert-warning mt-3 mb-0">
      <span>{labels.labelLoading}</span>
    </div>
  {:catch exception}
    <div class="alert alert-danger mt-3 mb-0">
      <span>{labels.errorFatal}</span>
      <br />
      <span>{exception.error}</span>
    </div>
  {/await}
</div>
